import React, { Component } from 'react';

class Issue extends Component {

  constructor(props) {
    super(props);

    this.state = {
      url: props.location.state.url || '',
      issue: {}
    }
  }

  componentDidMount() {
    this.getIssue();
  }

  getIssue() {
    fetch(this.props.location.state.url, {headers: {Accept: 'application/vnd.github.v3.html'}})
      .then(data => data.json())
      .then(data => {
        this.setState({ issue: data });
      })
  }

  issue() {
    return  (this.state.issue && this.state.issue.body_html) || ''
  }

  render() {
    return (
      <div className="issue">
        <h1>{this.state.issue && this.state.issue.title}</h1>
        <div className="markdown-body" dangerouslySetInnerHTML={{ __html: this.issue() }}>
        </div>
      </div>
    );
  }
}

export default Issue;



