import React, { Component } from 'react';
import { Link } from "react-router-dom";
import { format } from 'date-fns';


class Issues extends Component {
  state = {
    issue: {},
    issues: [],
    labels: [],
    owner: 'duobe',
    repo: 'stepByStep',
    // owner: 'facebook',
    // repo: 'create-react-app',
    page: this.props.match && parseInt(this.props.match.params.id) || 1,
    per_page: 10,
    total_count: 0,
    total_page_num: 0,
  }

  componentDidMount() {
    this.getLabels();
    this.getIssues()
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.match.params.id !== this.props.match.params.id) {
      this.setState({
        page: parseInt(nextProps.match.params.id, 10)
      }, () => {
        this.getIssues()
      });
    }
  }

  getIssues(label = '') {
    const url = new URL('https://api.github.com/search/issues')
    const { owner, repo, page, per_page } = this.state
    let q = `state:open repo:${owner}/${repo}`
    q += label && ` label:${label}`
    const params = {
      sort: 'created',
      order: 'desc',
      per_page,
      page,
      q,
    }

    Object.keys(params).forEach(key => url.searchParams.append(key, params[key]))

    fetch(url, { headers: { Accept: 'application/vnd.github.v3.html' } })
      .then(data => data.json())
      .then(data => {
        const total_page_num = parseInt((data.total_count + per_page - 1) / per_page)
        
        this.setState({ issues: data.items, total_count: data.total_count, total_page_num, page })
      })
  }

  getIssuesByPage(page) {
    this.setState({ page }, () => {
      this.getIssues();
    })
  }

  getLabels() {
    const { owner, repo } = this.state
    const url = `https://api.github.com/repos/${owner}/${repo}/labels`
    fetch(url)
      .then(data => data.json())
      .then(data => {
        this.setState({ labels: data })
      })
  }

  renderLabels() {
    return (
      <div className="labels">
        <div>FILTER</div>
        {
          this.state.labels && this.state.labels.map(label => (
            <label
              onClick={() => this.getIssues(label.name)}
              key={label.id}
              className="label"
            >{label.name.toUpperCase()}</label>
          ))
        }
      </div>
    )
  }

  renderIssues() {
    return (
      <div className="issues">
        {
          this.state.issues && this.state.issues.map(issue => (
            <div key={issue.id}>
              <span>{format(new Date(issue.created_at), 'MM/DD/YYYY')}</span>
              {
                issue.labels.map(label => <label key={label.id}>{label.name.toUpperCase()}</label>)
              }
              <Link
                title={issue.title}
                to={{ pathname: `/issues/${issue.number}`, state: { url: issue.url } }}
              >
                {issue.title}
              </Link>
            </div>
          ))
        }
      </div>
    )
  }

  renderPagination() {
    const { total_page_num, page, per_page } = this.state
    if (total_page_num <= per_page) return
    let array = []
    for (let i = 1; i < total_page_num; i++) {
      array.push(
        <Link
          key={i}
          className={`page${page === i ? ' active' : ''}`}
          to={`/page/${i}`}
        >
          {i}
        </Link>)
    }
    return (
      <div className="pagination">
        {array}
      </div>
    )
  }

  render() {
    
    return (
      <div>
        {this.renderLabels()}
        {this.renderIssues()}
        {this.renderPagination()}
      </div>
    );
  }
}

export default Issues;



