import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Issues from './Issues';

class App extends Component {
  constructor() {
    super();

    this.state = {
      issues: []
    }
  }

  render() {
    return (
      <div className="App">
        {/* <header className="App-header">
          THE FULL LIST
          <h1 className="App-title">Archives</h1>
          <p>This is all of writing I've published on this domain.</p>
          <p>One day there will be enough for a search bar.</p>
        </header> */}
        <div className="App-content">
          <Issues issues={this.state.issues} />
        </div>
      </div>
    );
  }
}

export default App;



